.home-link {
  position: relative;
  border: none;
}

.home-link:active, .home-link:focus ,
.home-link:not(:disabled):not(.disabled).active, .home-link:not(:disabled):not(.disabled):active,
.home-link.active, .home-link.active:focus{
  border: none !important;
  box-shadow: none !important;
}

.home-link::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 10%;
  background-color: rgba(0, 0, 0, 0.5);
  top:100%;
  left: 0%;
  -webkit-transition: 0.3s all ease;
  transition: 0.3s all ease;
