          transform: translateY(30px);
  opacity: 0;
}
.home-link:hover::after {
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  opacity: 0.7;
}

.home-link.home-link__visited::after {
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  opacity: 0.7;
