  background-attachment: fixed;
  background-position: center;
  background-size: cover;
}
.testimonial-section:after {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    content: "";
    background: #000;
    opacity: 0.8;
}

.testimonial-col .img {
  width: 100px;
  height: 100px;
  overflow: hidden;
  display: inline-block;
  border-radius: 50%;
  margin-bottom: 25px;
}
.testimonial-col p {
  color: #fff;
  font-weight: 200;
}

.testimonial-col h6 {
  color: #fff;
  font-weight: 200;
}
.testimonial-col h6 span {
  color: #fff;
}

.testimonial-section .owl-dots {
  bottom: -40px;
}

/*-----------------------------------------
