-----------------------------------------*/
.blog-item {
  margin: 15px 0;
}
.blog-item .blog-content {
  border: 1px solid #ddd;
  padding: 15px;
}
.blog-item .blog-content .post-meta {
  font-size: 13px;
}
.blog-item .blog-content .post-meta a {
  color: #333;
  font-weight: 600;
}
.blog-item .blog-content h4 {
  margin: 0;
  padding: 0px 0 8px;
  font-size: 17px;
}
.blog-item .blog-content h4 a {
  font-weight: 600;
  text-decoration: none;
}
.blog-item .blog-content h4 a:hover {
  color: #000;
}

.blog-item .blog-content .read-more {
  font-weight: 600;
  text-transform: uppercase;
  color: #000;
  font-size: 12px;
  letter-spacing: 1px;
}

.blog-section .owl-dots {
  position: relative;
}

.blog-section .owl-dots .owl-dot {
  border: 1px solid #222;
}
.blog-section .owl-dots .owl-dot.active {
  background: #222;
}

/*-----------------------------------------
* 15. Contact Us
-----------------------------------------*/
.contact-info-box {
  padding: 30px 10px;
}
.contact-info-box h2 {
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 40px;
  letter-spacing: 1px;
}
.contact-info {
  padding: 0 0px 30px;
  position: relative;
  text-align: center;
}
.contact-info h4 {
  font-size: 16px;
  font-weight: 600;
}
.contact-info i {
  position: absolute;
  color: #212529;
  top: 0;
  left: 0;
  width: 29px;
  text-align: center;
  opacity: 0.8;
  font-size: 19px;
}
.contact-info p {
  margin: 0;
  padding: 0;
  font-size: 13px;
  color: #212529;
  letter-spacing: 1px;
}

.contact-info-box .social-icons a {
    color: #212529;
}
.contact-info-box .social-icons a:hover {
  color: #212529;
  opacity: 0.6;
}

.contact-map {
  height: 100%;
  padding: 20px;
  box-shadow: 0 0 14px rgba(0, 0, 0, 0.13);
  background: #fff;
}
.contact-map .embed-responsive-16by9:before {
  padding-top: 60.25%;
}

.contact-form {
  padding: 20px;
  box-shadow: 0 0 14px rgba(0, 0, 0, 0.13);
}
.contact-form h2 {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 30px;
  letter-spacing: 1px;
}
.form-control {
  border-radius: 0;
  font-size: 14px;
  padding: 10px 15px 7px;
  box-shadow: none !important;
}

@media (max-width: 767px){
  .contact-form {
      padding: 15px;
  }
  .contact-form h2 {
    margin-bottom: 20px;
  }
}

