  padding: 0;
}
.social-icons li {
  display: inline-block;
  vertical-align: top;
  margin-right: 12px;
}
.social-icons li a {
  color: #8e8e8e;
  font-size: 18px;
  -webkit-transition: ease all 0.2s;
          transition: ease all 0.2s;
}
.social-icons li a:hover {
  color: #000;
}

